odoListStore} from \"@/stores/TodoListStore\";\r\n\r\nconst todolistStore = useTodoListStore();\r\n\r\nimport {defineProps, onMounted} from \"vue\";\r\n\r\nconst props = defineProps({\r\n  todoItem: {\r\n    type: Object,\r\n    required: true,\r\n  },\r\n});\r\nconst customClass=\"todolist__title\";\r\n\r\nonMounted(() => {\r\n  todolistStore.getTasks(props.todoItem.id);\r\n\r\n});\r\nconst addItemToTodoList = (todoItemID, title) => {\r\n  todolistStore.addTask(todoItemID, title)\r\n};\r\n\r\nconst editTitleHandler = (todoItemID ,value) => {\r\n todolistStore.updateTitleTodolist(todoItemID,value)\r\n}\r\n\r\n</script>\r\n\r\n<style>\r\nbutton {\r\n  margin: 0 !important;\r\n}\r\n.todolist {\r\n  width: 470px;\r\n  padding: 20px;\r\n  border: 1px solid #c25205;\r\n  margin-bottom: 20px;\r\n}\r\n.todolist__title {\r\n  font-size: 25px;\r\n  color: #421111;\r\n  text-transform: uppercase;\r\n}\r\n\r\n.todolist__title-wrap {\r\n  display: flex;\r\n  align-items: center;\r\n  gap: 20px;\r\n  justify-content: space-between;\r\n  margin-bottom: 25px;\r\n}\r\n\r\n.todolist__item {\r\n  margin-top: 20px;\r\n}\r\n.todolist__delete {\r\nbackground: transparent;\r\n  border: none;\r\n}\r\n\r\n\r\n</style>"],"sourceRoot":""}]);
