___CSS_LOADER_EXPORT___.push([module.id, ".posts{padding-top:50px;max-width:1000px;margin:0 auto}.posts__btn-creat{background:coral;cursor:pointer;padding:10px;border:none;border-radius:10px;color:#f0f8ff;font-size:18px}.posts__btns{margin-top:17px;justify-content:space-between;margin-bottom:20px}.page,.posts__btns{display:flex;align-items:center}.page{margin:20px 0 50px 0}.posts__modal-title{margin-bottom:25px}.page__count{margin-right:15px;border:1px solid coral!important;padding:10px;cursor:pointer}.page--current{background:coral!important;color:azure}.posts__select{margin-top:15px;padding:10px}.hightlight{color:coral}", "",{"version":3,"sources":["webpack://./src/pages/PostsPage.vue"],"names":[],"mappings":"AAiJA,OACE,gBAAiB,CACjB,gBAAiB,CACjB,aACF,CAEA,kBACE,gBAAiB,CACjB,cAAe,CACf,YAAa,CACb,WAAY,CACZ,kBAAmB,CACnB,aAAgB,CAChB,cACF,CACA,aACE,eAAgB,CAEhB,6BAA8B,CAE9B,kBACF,CACA,mBALE,YAAa,CAEb,kBAQF,CALA,MAGE,oBAEF,CACA,oBACE,kBACF,CACA,aACE,iBAAkB,CAClB,gCAAkC,CAClC,YAAa,CACb,cACF,CACA,eACE,0BAA4B,CAC5B,WACF,CACA,eACE,eAAgB,CAChB,YACF,CACA,YACE,WACF","sourcesContent":["<template>\r\n  <div class=\"posts\">\r\n    <h1>Страница с постами</h1>\r\n    <my-input v-model=\"searchQuery\" placeholder=\"поиск...\"/>\r\n    <div class=\"posts__btns\">\r\n      <my-button class=\"posts__btn-creat\" @click=\"openModalHandler\">Создать пост</my-button>\r\n      <div>\r\n        <h4> Сортировка постов:</h4>\r\n      <my-select :options=\"sortOptions\" v-model=\"selectedSort\" class=\"posts__select\"></my-select>\r\n      </div>\r\n    </div>\r\n\r\n    <my-modal v-model:show=\"showModal\">\r\n      <div>\r\n        <h2 class=\"posts__modal-title\">Создание поста:</h2>\r\n        <post-form @create=\"addPost\"/> <!--  так как мы добавляли slot, то мы можем внутрь закидывать компонеты-->\r\n      </div>\r\n    </my-modal>\r\n\r\n    <post-list v-if=\"!isPostLoading\"\r\n               :posts=\"filteredPosts\"\r\n               @remove=\"deleteHandler\"/>\r\n    <my-loader v-else/>\r\n    <div class=\"page\">\r\n      <div v-for=\"pageNumber in totalPages\"\r\n           :key=\"pageNumber\"\r\n           class=\"page__count\"\r\n           :class=\"{'page--current':page === pageNumber}\"\r\n           @click=\"pageNumberHandler(pageNumber)\"\r\n      >\r\n        {{pageNumber}}\r\n      </div>\r\n    </div>\r\n  </div>\r\n</template>\r\n\r\n<script setup>\r\nimport MyModal from \"@/components/MyModal.vue\";\r\nimport PostForm from \"@/components/PostForm.vue\";\r\nimport PostList from \"@/components/PostList.vue\";\r\nimport MyButton from \"@/components/common/MyButton.vue\";\r\nimport MySelect from \"@/components/common/MySelect.vue\";\r\nimport MyInput from \"@/components/common/MyInput.vue\";\r\nimport MyLoader from \"@/components/common/MyLoader.vue\";\r\nimport {computed, onMounted, ref, watch} from 'vue';\r\n\r\nimport { useFetchPosts } from \"@/hooks/useFetchPosts\";\r\n//начальное состояние постов-инициализация\r\n let posts = ref([]);\r\n //импорт хука \r\nlet { isPostLoading, totalPages, page, limit, fetchPosts } = useFetchPosts(posts);\r\n\r\n// let isPostLoading = ref(false);\r\n\r\n// ушла в хук useFetchPosts:\r\n\r\n//удаление поста\r\nconst deleteHandler = (post) => {\r\n  posts.value = posts.value.filter(p => p.id !== post.id);\r\n}\r\n\r\n//открытие модального окна\r\nlet showModal = ref(false);\r\nconst openModalHandler = () => {\r\n  showModal.value = true;\r\n}\r\n\r\n//добавление нового поста\r\nconst addPost = (newPost) => {\r\n  posts.value.unshift(newPost);\r\n  showModal.value = false;\r\n\r\n};\r\n//постраничный вывод постов\r\n// ушла в хук useFetchPosts:\r\n// let page=ref(1); //тут будем хранить номер страницы\r\n// const limit=ref(10); //количество постов на одной странице\r\n// let totalPages=ref(0) ;\r\n\r\nconst pageNumberHandler=(pageNumber)=> {\r\n  page.value = pageNumber;\r\n  fetchPosts();\r\n}\r\n// ушла в хук useFetchPosts:\r\n//запрос за постами с сервера\r\n// onMounted(() => {\r\n//   fetchPosts();\r\n// })\r\n\r\n//функция ушла в хук useFetchPosts\r\n// async function fetchPosts() {\r\n//   try {\r\n//     isPostLoading.value = true;\r\n//     const response = await axios.get('https://jsonplaceholder.typicode.com/posts', {\r\n//       params:{_page:page.value,\r\n//         _limit:limit.value}\r\n//     });\r\n//     const totalPostsHeader = response.headers['x-total-count'];\r\n//\r\n//     if (totalPostsHeader) {\r\n//       totalPages.value = Math.ceil(Number(totalPostsHeader) / limit.value);\r\n//     }\r\n//     posts.value = response.data;\r\n//   } catch (e) {\r\n//     alert('Ошибка')\r\n//   }\r\n//   finally {\r\n//     isPostLoading.value = false;\r\n//   }\r\n// }\r\n\r\n\r\n//сортировка\r\nlet selectedSort = ref('');\r\nlet sortOptions = [\r\n  {value: 'title', name: 'По названию'},\r\n  {value:'body', name: 'По содержимому'},\r\n];\r\n\r\nconst sortedPosts = computed(() => {\r\n  return [...posts.value].sort((a, b) => a[selectedSort]?.localeCompare(b[selectedSort]));\r\n\r\n});\r\n//не надо нам оно уже:\r\nwatch(selectedSort, (newValue) => {\r\n  posts.value.sort((a, b) => {\r\n    return a[newValue]?.localeCompare(b[newValue]);\r\n  });\r\n});\r\n\r\n//поиск и сортировка\r\nlet searchQuery = ref('');\r\nconst filteredPosts = computed(() => {\r\n  const query = searchQuery.value.trim().toLowerCase();\r\n  const selected = selectedSort.value;\r\n\r\n  return posts.value\r\n      .filter(post => post.title.toLowerCase().includes(query))//фильтрация в титле\r\n      .sort((a, b) => a[selected]?.localeCompare(b[selected]));\r\n});\r\n\r\n\r\n</script>\r\n\r\n<style>\r\n.posts {\r\n  padding-top: 50px;\r\n  max-width: 1000px;\r\n  margin: 0 auto;\r\n}\r\n\r\n.posts__btn-creat {\r\n  background: coral;\r\n  cursor: pointer;\r\n  padding: 10px;\r\n  border: none;\r\n  border-radius: 10px;\r\n  color: aliceblue;\r\n  font-size: 18px;\r\n}\r\n.posts__btns {\r\n  margin-top: 17px;\r\n  display: flex;\r\n  justify-content: space-between;\r\n  align-items: center;\r\n  margin-bottom: 20px;\r\n}\r\n.page{\r\n  display: flex;\r\n  align-items: center;\r\n  margin: 20px 0 50px 0;\r\n\r\n}\r\n.posts__modal-title {\r\n  margin-bottom: 25px;\r\n}\r\n.page__count {\r\n  margin-right: 15px;\r\n  border: 1px solid coral !important;\r\n  padding: 10px;\r\n  cursor: pointer;\r\n}\r\n.page--current {\r\n  background: coral !important;\r\n  color: azure;\r\n}\r\n.posts__select {\r\n  margin-top: 15px;\r\n  padding: 10px;\r\n}\r\n.hightlight {\r\n  color: coral;\r\n}\r\n</style>"],"sourceRoot":""}]);
