re.setActiveTab(number); //это чтоб функцию сделать короче\r\n};\r\n\r\n</script>\r\n<style>\r\n.movies,\r\n.search {\r\n  max-width: 1000px;\r\n  margin: 0 auto;\r\n}\r\n.tabs {\r\n  display: flex;\r\n  justify-content: center;\r\n  margin-top: 50px;\r\n  margin-bottom: 30px;\r\n}\r\n.btn_green {\r\n  background: #37df5c !important;\r\n  color: white !important;\r\n}\r\n\r\n</style>\r\n"],"sourceRoot":""}]);
