ed (посмотрели-не посмотрели)-->\r\n          <span v-if=\"!movie.isWatched\">Watched</span>\r\n          <span v-else>Unwatched</span>\r\n        </button>\r\n        <button class=\"btn movie-buttons-delete\"  @click=\"movieStore.deleteMovie(movie.id)\">Delete</button>\r\n      </div>\r\n      <div class=\"movie-buttons\" v-else>\r\n        <button class=\"btn btn_green\"  @click=\"searchStore.addToUserMovies(movie)\">Add</button>\r\n      </div>\r\n    </div>\r\n  </div>\r\n</template>\r\n\r\n<script setup>\r\nimport {useMovieStore} from \"@/stores/MoviStore\";\r\nconst movieStore = useMovieStore();\r\n\r\nimport { useSearchStore } from \"@/stores/SearchStore\";\r\nconst searchStore = useSearchStore();\r\n\r\nconst props = defineProps({\r\n  movie: {\r\n    type: Object,\r\n    required: true,\r\n    default: () => {},\r\n  },\r\n  isSearch: {\r\n    type: Boolean,\r\n    required: false,\r\n    default: false,\r\n  },\r\n});\r\n</script>\r\n\r\n<style lang=\"css\" scoped>\r\n.movie {\r\n  display: grid;\r\n  grid-template-columns: 200px 1fr;\r\n  column-gap: 30px;\r\n  margin-bottom: 20px;\r\n  border: 2px solid #efefef;\r\n  padding: 10px;\r\n  border-radius: 10px;\r\n}\r\n\r\n.movie-accept {\r\n  margin-right: 10px;\r\n}\r\n\r\n.movie-img {\r\n  width: 200px;\r\n  height: 200px;\r\n  object-fit: cover;\r\n  border-radius: 50%;\r\n}\r\n\r\n.movie-name {\r\n  display: flex;\r\n  align-items: center;\r\n  font-size: 20px;\r\n  margin-bottom: 20px;\r\n}\r\n\r\n.movie-overview {\r\n  display: block;\r\n  margin-bottom: 20px;\r\n}\r\n\r\n.movie-buttons {\r\n  display: flex;\r\n  align-items: center;\r\n  justify-content: center;\r\n}\r\n\r\n.movie-buttons-watched {\r\n  color: #fff;\r\n  background: #1eb4c3;\r\n}\r\n\r\n.movie-buttons-watched__icon {\r\n  width: 15px;\r\n  margin-left: 10px;\r\n}\r\n\r\n.movie-buttons-delete {\r\n  color: #fff;\r\n  background: #ff2a2a;\r\n}\r\n</style>\r\n"],"sourceRoot":""}]);
