___CSS_LOADER_EXPORT___.push([module.id, ".posts{padding-top:50px;max-width:1000px;margin:0 auto}.posts__btn-creat{background:coral;cursor:pointer;padding:10px;border:none;border-radius:10px;color:#f0f8ff}.posts__btns{justify-content:space-between}.page,.posts__btns{display:flex;align-items:center}.page{margin-top:20px}.page__count{margin-right:15px;border:1px solid #8a2be2;padding:10px;cursor:pointer}.page--current{background:#8a2be2;color:azure}.element{height:30px;background:coral;width:100%}", "",{"version":3,"sources":["webpack://./src/pages/PostsObservePage.vue"],"names":[],"mappings":"AA0HA,OACE,gBAAiB,CACjB,gBAAiB,CACjB,aACF,CAEA,kBACE,gBAAiB,CACjB,cAAe,CACf,YAAa,CACb,WAAY,CACZ,kBAAmB,CACnB,aACF,CACA,aAEE,6BAEF,CACA,mBAJE,YAAa,CAEb,kBAOF,CALA,MAGE,eAEF,CACA,aACE,iBAAkB,CAClB,wBAA4B,CAC5B,YAAa,CACb,cACF,CACA,eACE,kBAAsB,CACtB,WACF,CACA,SACE,WAAY,CACZ,gBAAiB,CACjB,UACF","sourcesContent":["<template>\r\n  <div class=\"posts\">\r\n    <h1>Страница с постами</h1>\r\n    <my-input v-model=\"searchQuery\" placeholder=\"поиск...\"/>\r\n    <div class=\"posts__btns\">\r\n      <my-button class=\"posts__btn-creat\" @click=\"openModalHandler\">Создать пост</my-button>\r\n      <my-select :options=\"sortOptions\" v-model=\"selectedSort\" ></my-select>\r\n    </div>\r\n\r\n    <my-modal v-model:show=\"showModal\">\r\n      <div>\r\n        <h2>Создание поста:</h2>\r\n        <post-form @create=\"addPost\"/> <!--  так как мы добавляли slot, то мы можем внутрь закидывать компонеты-->\r\n      </div>\r\n    </my-modal>\r\n\r\n    <post-list\r\n        :posts=\"filteredPosts\"\r\n        @remove=\"deleteHandler\"/>\r\n    <div ref=\"observerItem\" class=\"element\"></div>\r\n  </div>\r\n</template>\r\n\r\n<script setup>\r\nimport MyModal from \"@/components/MyModal.vue\";\r\nimport PostForm from \"@/components/PostForm.vue\";\r\nimport PostList from \"@/components/PostList.vue\";\r\nimport MyButton from \"@/components/common/MyButton.vue\";\r\nimport MySelect from \"@/components/common/MySelect.vue\";\r\nimport MyInput from \"@/components/common/MyInput.vue\";\r\n\r\nimport {computed, onMounted, ref, watch} from 'vue';\r\nimport axios from \"axios\";\r\n\r\nlet posts = ref([]);\r\n\r\n\r\nconst deleteHandler = (post) => {\r\n  posts.value = posts.value.filter(p => p.id !== post.id);\r\n}\r\n\r\nlet showModal = ref(false);\r\nconst openModalHandler = () => {\r\n  showModal.value = true;\r\n}\r\n\r\nconst addPost = (newPost) => {\r\n  posts.value.push(newPost);\r\n  showModal.value = false;\r\n\r\n};\r\nlet page=ref(1); //тут будем хранить номер страницы\r\nconst limit=ref(10); //количество постов на одной странице\r\n\r\nasync function fetchPostsObserve() {\r\n  try {\r\n    const response = await axios.get('https://jsonplaceholder.typicode.com/posts', {\r\n      params: {\r\n        _page:page.value,\r\n        _limit:limit.value}\r\n    });\r\n    page.value = page.value + 1\r\n\r\n    posts.value = [...posts.value, ...response.data];\r\n  } catch (e) {\r\n    alert(e)\r\n  }\r\n}\r\nconst observerItem = ref(null);\r\n\r\nonMounted(() => {\r\n    fetchPostsObserve();\r\n  let options = {\r\n    rootMargin: \"0px\",\r\n    threshold: 0.1, //элемент должен показаться полностью в зоне пересечения\r\n  }\r\n  const callback = function(entries, observer) {\r\n    if (entries[0].isIntersecting) {\r\n      fetchPostsObserve();\r\n    }\r\n\r\n  };\r\n  let observer = new IntersectionObserver(callback, options);\r\n  observer.observe(observerItem.value) //будем следить за этим элементом\r\n\r\n})\r\n\r\n\r\n\r\n//сортировка\r\nlet selectedSort = ref('');\r\nlet sortOptions = [\r\n  {value: 'title', name: 'По названию'},\r\n  {value:'body', name: 'По содержимому'},\r\n];\r\n\r\nconst sortedPosts = computed(() => {\r\n  return [...posts.value].sort((a, b) => a[selectedSort]?.localeCompare(b[selectedSort]));\r\n\r\n});\r\n//не надо нам оно уже:\r\nwatch(selectedSort, (newValue) => {\r\n  posts.value.sort((a, b) => {\r\n    return a[newValue]?.localeCompare(b[newValue]);\r\n  });\r\n});\r\n\r\n//поиск\r\nlet searchQuery = ref('');\r\nconst filteredPosts = computed(() => {\r\n  const query = searchQuery.value.trim().toLowerCase();\r\n  const selected = selectedSort.value;\r\n\r\n  return posts.value\r\n      .filter(post => post.title.toLowerCase().includes(query))//фильтрация в титле\r\n      .sort((a, b) => a[selected]?.localeCompare(b[selected]));\r\n});\r\n\r\n\r\n</script>\r\n\r\n<style>\r\n.posts {\r\n  padding-top: 50px;\r\n  max-width: 1000px;\r\n  margin: 0 auto;\r\n}\r\n\r\n.posts__btn-creat {\r\n  background: coral;\r\n  cursor: pointer;\r\n  padding: 10px;\r\n  border: none;\r\n  border-radius: 10px;\r\n  color: aliceblue;\r\n}\r\n.posts__btns {\r\n  display: flex;\r\n  justify-content: space-between;\r\n  align-items: center;\r\n}\r\n.page{\r\n  display: flex;\r\n  align-items: center;\r\n  margin-top: 20px;\r\n\r\n}\r\n.page__count {\r\n  margin-right: 15px;\r\n  border: 1px solid blueviolet;\r\n  padding: 10px;\r\n  cursor: pointer;\r\n}\r\n.page--current {\r\n  background: blueviolet;\r\n  color: azure;\r\n}\r\n.element{\r\n  height: 30px;\r\n  background: coral;\r\n  width: 100%;\r\n}\r\n</style>"],"sourceRoot":""}]);
