___CSS_LOADER_EXPORT___.push([module.id, ".submit-form{margin:50px auto 0 auto;max-width:600px;display:flex;flex-direction:column;justify-content:space-between;align-items:flex-start;min-height:300px;padding:15px}.submit-btn,.submit-form{background:#fff;color:coral;font-size:18px;border:1px solid coral}.submit-btn{display:block;cursor:pointer;padding:10px;transition:.5s}.submit-btn:hover{background:coral;transition:.5s;color:#fff}.form-control{width:400px}.form-check{margin-right:10px}button{margin-top:15px}", "",{"version":3,"sources":["webpack://./src/pages/Auth.vue"],"names":[],"mappings":"AAgGA,aACE,uBAAwB,CACxB,eAAgB,CAChB,YAAa,CACb,qBAAsB,CACtB,6BAA8B,CAC9B,sBAAuB,CACvB,gBAAiB,CAGjB,YAGF,CACA,yBANE,eAAiB,CACjB,WAAY,CAEZ,cAAe,CACf,sBAWF,CATA,YACE,aAAc,CACd,cAAe,CAGf,YAAa,CAGb,cACF,CACA,kBACE,gBAAiB,CACjB,cAAgB,CAChB,UACF,CAEA,cACE,WACF,CAEA,YACE,iBACF,CAEA,OACE,eACF","sourcesContent":["<template>\r\n  <form @submit.prevent=\"checkForm\" class=\"submit-form\">\r\n    <h1>Авторизация:</h1>\r\n    <div class=\"form-group\">\r\n      <my-input\r\n                v-model.trim=\"form.email\"\r\n                label=\"Почта\"\r\n                id=\"email\"\r\n                type=\"email\"\r\n                class=\"form-control\"\r\n                :class=\"emailError ? 'is-invalid' : ''\"\r\n                placeholder=\"напишите емэйл\"\r\n      />\r\n      <p v-if=\"emailDirty && !emailRequired\" class=\"invalid-feedback\">\r\n        Обязательное поле\r\n      </p>\r\n      <p v-if=\"emailDirty && !emailEmail\" class=\"invalid-feedback\">\r\n        Email некорректный\r\n      </p>\r\n    </div>\r\n    <div class=\"form-group\">\r\n      <my-input v-model.trim=\"form.password\"\r\n                id=\"password\"\r\n                type=\"password\"\r\n                label=\"Пароль\"\r\n                placeholder=\"напишите пароль\"\r\n                class=\"form-control\"\r\n                :class=\"passwordError ? 'is-invalid' : ''\"\r\n\r\n      />\r\n      <p v-if=\"passwordDirty && !passwordRequired\" class=\"invalid-feedback\">\r\n        Обязательное поле\r\n      </p>\r\n    </div>\r\n    <my-button class=\"submit-btn\" type=\"submit\">Отправить</my-button>\r\n  </form>\r\n</template>\r\n\r\n<script setup>\r\nimport MyInput from \"@/components/common/MyInput.vue\";\r\nimport MyButton from \"@/components/common/MyButton.vue\";\r\nimport { required, minLength, email } from 'vuelidate/lib/validators';\r\nimport { useVuelidate } from '@vuelidate/core';\r\nimport {ref, reactive, onMounted, watchEffect} from 'vue';\r\nimport { useRouter } from 'vue-router';\r\nconst router = useRouter();\r\n\r\nimport {useAuthStore} from \"@/stores/AuthStore\";\r\nconst authStore = useAuthStore();\r\nimport {useTodoListStore} from \"@/stores/TodoListStore\";\r\nconst todolistStore = useTodoListStore();\r\n\r\n\r\nconst form = ref({\r\n  email: '',\r\n  password: '',\r\n});\r\n\r\nconst validations = {\r\n  form: {\r\n    email: { required, email },\r\n    password: { required, minLength: minLength(6) },\r\n  },\r\n};\r\n\r\nconst v = useVuelidate(validations, form);\r\n\r\nconst emailError = v.value.form.email.$error;\r\nconst emailDirty = v.value.form.email.$dirty;\r\nconst emailRequired = v.value.form.email.required;\r\nconst emailEmail = v.value.form.email.email;\r\n\r\nconst passwordError = v.value.form.password.$error;\r\nconst passwordDirty = v.value.form.password.$dirty;\r\nconst passwordRequired = v.value.form.password.required;\r\n\r\nconst checkForm = async () => {\r\n  if (!v.value.$error) {\r\n    const formData = reactive({\r\n      email: form.value.email,\r\n      password: form.value.password\r\n    });\r\n\r\n    await authStore.login(formData);\r\n    if (authStore.isLoggetIn) {\r\n      console.log(authStore.isLoggetIn)\r\n     await router.push('/todo');\r\n\r\n    }\r\n  } else {\r\n    v.value.$touch();\r\n  }\r\n};\r\n</script>\r\n\r\n<style>\r\n.submit-form {\r\n  margin: 50px auto 0 auto;\r\n  max-width: 600px;\r\n  display: flex;\r\n  flex-direction: column;\r\n  justify-content: space-between;\r\n  align-items: flex-start;\r\n  min-height: 300px;\r\n  background: white;\r\n  color: coral;\r\n  padding: 15px;\r\n  font-size: 18px;\r\n  border: 1px solid coral;\r\n}\r\n.submit-btn {\r\n  display: block;\r\n  cursor: pointer;\r\n  background: white;\r\n  color: coral;\r\n  padding: 10px;\r\n  font-size: 18px;\r\n  border: 1px solid coral;\r\n  transition: 0.5s;\r\n}\r\n.submit-btn:hover {\r\n  background: coral;\r\n  transition: 0.5s;\r\n  color: white;\r\n}\r\n\r\n.form-control {\r\n  width: 400px;\r\n}\r\n\r\n.form-check {\r\n  margin-right: 10px;\r\n}\r\n\r\nbutton {\r\n  margin-top: 15px;\r\n}\r\n</style>\r\n\r\n\r\n\r\n\r\n"],"sourceRoot":""}]);
